	module.exports = {"data":{"markdownRemark":{"html":"<h2>Text Shadow</h2>\n<p>The <code>text-shadow</code> property adds shadow to text. </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>  <span class=\"token property\">text-shadow</span><span class=\"token punctuation\">:</span> offset-x offset-y blur-radius color<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>offset-x</h3>\n<p>Sets the horizontal distance from the text. Accepts both positive and negative values. Positive values place shadow to the right of the text while a negative value places the shadow to the left.</p>\n<p>The <code>offset-x</code> value is required.</p>\n<h3>offset-y</h3>\n<p>Sets the vertical distance from the text. Accepts both positive and negative values. Positive values place shadow to below the text while a negative value places the shadow above.</p>\n<p>The <code>offset-y</code> value is required.</p>\n<h3>blur-radius</h3>\n<p>Sets the length of the shadow’s blur. The larger the value the wider and lighter the shadow becomes.</p>\n<p>The <code>blur-radius</code> value is optional.</p>\n<h3>color</h3>\n<p>Sets the color of the shadow.</p>\n<p>The <code>color</code> value is optional.</p>\n<h3>Example</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>  <span class=\"token property\">text-shadow</span><span class=\"token punctuation\">:</span> 1px -2px 5px #a4a4a4<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>Example of multiple shadows</h3>\n<p>Multiple shadows can be used for giving text a solid outline on all 4 directions, like a bubble-writing effect.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>  <span class=\"token property\">text-shadow</span><span class=\"token punctuation\">:</span> -1px 0 1px black, 0 1px 1px black, 1px 0 1px black, 0 -1px 1px black<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>More Information:</h3>\n<p>MDN Documentation: <a href='https://developer.mozilla.org/en-US/docs/Web/CSS/text-shadow' target='_blank' rel='nofollow'>MDN</a></p>","fields":{"slug":"/css/text-shadow/"},"frontmatter":{"title":"Text Shadow"}}},"pathContext":{"slug":"/css/text-shadow/"}}

